// Pricing Tiers

.pricing-tiers {
  @extend .grid;
  @extend .grid.spaced;

  header {
    margin-bottom: $padding-sm;

    .title {
      margin: 0;
      @include h2;

      @include media-md {
        @include paragraph-style(36,42);
      }
    }

    p {
      margin: 0;
      color: color(blue-light);
    }
  }

  p {
    margin: 0;
    color: color(gray-dark);
    @include ginto-normal-light;
  }

  .pricing-tier {
    > .-inner {
      border-radius: 12px;
      padding: $padding-sm;
      background-color: color(gray-light);
    }

    + .pricing-tier {
      margin-top: $padding-sm;
    }

    ul {
      margin-top: $padding-sm;
    }

    .cta {
      margin: $padding-md 0 0;
    }

    @include media-md {
      > .-inner {
        height: 100%;
        display: flex;
        padding: $padding-md;
        flex-direction: column;
      }

      + .pricing-tier {
        margin-top: 0;
      }

      .cta {
        flex-grow: 1;
        display: flex;
        align-items: flex-end;
      }
    }
  }
}